import React, { useState } from 'react';
import axios from 'axios';
import { ChefHat, Sparkles, Download, RotateCcw } from 'lucide-react';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';
import './index.css';

function App() {
  const [ingredients, setIngredients] = useState(['', '', '', '', '']);
  const [servings, setServings] = useState(4);
  const [recipe, setRecipe] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleIngredientChange = (index, value) => {
    const newIngredients = [...ingredients];
    newIngredients[index] = value;
    setIngredients(newIngredients);
  };

  const generateRecipe = async () => {
    const filledIngredients = ingredients.filter(ing => ing.trim() !== '');
    
    if (filledIngredients.length === 0) {
      setError('Please enter at least one ingredient!');
      return;
    }

    setLoading(true);
    setError('');
    setRecipe(null);

    try {
      const response = await axios.post('/api/generate-recipe', {
        ingredients: filledIngredients,
        servings: servings
      });

      setRecipe(response.data.recipe);
    } catch (err) {
      setError(err.response?.data?.error || 'Something went wrong. Please try again!');
    } finally {
      setLoading(false);
    }
  };

  const resetForm = () => {
    setIngredients(['', '', '', '', '']);
    setServings(4);
    setRecipe(null);
    setError('');
  };

  const exportToPDF = async () => {
    if (!recipe) return;

    try {
      const pdf = new jsPDF('p', 'mm', 'a4');
      const pageWidth = pdf.internal.pageSize.getWidth();
      const margin = 20;
      let yPos = margin;

      // Add title
      pdf.setFontSize(24);
      pdf.setFont('helvetica', 'bold');
      pdf.text(recipe.title, pageWidth / 2, yPos, { align: 'center' });
      yPos += 15;

      // Add description
      pdf.setFontSize(12);
      pdf.setFont('helvetica', 'italic');
      const description = pdf.splitTextToSize(recipe.description, pageWidth - 2 * margin);
      pdf.text(description, pageWidth / 2, yPos, { align: 'center' });
      yPos += description.length * 5 + 10;

      // Add meta info
      pdf.setFont('helvetica', 'normal');
      pdf.text(`Servings: ${recipe.servings} | Prep: ${recipe.prepTime} | Cook: ${recipe.cookTime}`, pageWidth / 2, yPos, { align: 'center' });
      yPos += 15;

      // Add ingredients
      pdf.setFontSize(16);
      pdf.setFont('helvetica', 'bold');
      pdf.text('Ingredients', margin, yPos);
      yPos += 10;

      pdf.setFontSize(11);
      pdf.setFont('helvetica', 'normal');
      recipe.ingredients.forEach((ingredient, index) => {
        if (yPos > 250) {
          pdf.addPage();
          yPos = margin;
        }
        pdf.text(`• ${ingredient}`, margin + 5, yPos);
        yPos += 6;
      });

      yPos += 10;

      // Add instructions
      pdf.setFontSize(16);
      pdf.setFont('helvetica', 'bold');
      pdf.text('Instructions', margin, yPos);
      yPos += 10;

      pdf.setFontSize(11);
      pdf.setFont('helvetica', 'normal');
      recipe.instructions.forEach((instruction, index) => {
        if (yPos > 250) {
          pdf.addPage();
          yPos = margin;
        }
        const wrappedText = pdf.splitTextToSize(`${index + 1}. ${instruction}`, pageWidth - 2 * margin);
        pdf.text(wrappedText, margin, yPos);
        yPos += wrappedText.length * 5 + 5;
      });

      // Add tips if available
      if (recipe.tips) {
        yPos += 10;
        if (yPos > 220) {
          pdf.addPage();
          yPos = margin;
        }
        pdf.setFontSize(16);
        pdf.setFont('helvetica', 'bold');
        pdf.text('Chef\'s Tips', margin, yPos);
        yPos += 10;

        pdf.setFontSize(11);
        pdf.setFont('helvetica', 'italic');
        const tipsText = pdf.splitTextToSize(recipe.tips, pageWidth - 2 * margin);
        pdf.text(tipsText, margin, yPos);
      }

      pdf.save(`${recipe.title.replace(/[^a-z0-9]/gi, '_').toLowerCase()}_recipe.pdf`);
    } catch (error) {
      console.error('Error generating PDF:', error);
      setError('Failed to generate PDF. Please try again.');
    }
  };

  return (
    <div className="app">
      <div className="container">
        <header className="header">
          <h1>🍳 AI Recipe Generator</h1>
          <p>Turn your ingredients into delicious recipes with AI magic!</p>
        </header>

        <div className="card">
          <form className="ingredient-form" onSubmit={(e) => { e.preventDefault(); generateRecipe(); }}>
            <div className="input-group">
              <label>Your Ingredients (up to 5)</label>
              <div className="ingredient-inputs">
                {ingredients.map((ingredient, index) => (
                  <input
                    key={index}
                    type="text"
                    className="ingredient-input"
                    placeholder={`Ingredient ${index + 1}`}
                    value={ingredient}
                    onChange={(e) => handleIngredientChange(index, e.target.value)}
                  />
                ))}
              </div>
            </div>

            <div className="input-group">
              <label>Number of Servings</label>
              <input
                type="number"
                className="servings-input"
                min="1"
                max="20"
                value={servings}
                onChange={(e) => setServings(parseInt(e.target.value) || 4)}
              />
            </div>

            <div style={{ display: 'flex', gap: '1rem', flexWrap: 'wrap' }}>
              <button
                type="submit"
                className="btn btn-primary"
                disabled={loading}
              >
                {loading ? (
                  <>
                    <div className="loading-spinner" style={{ width: '20px', height: '20px', margin: '0' }} />
                    Cooking up magic...
                  </>
                ) : (
                  <>
                    <Sparkles size={20} />
                    Generate Recipe
                  </>
                )}
              </button>

              <button
                type="button"
                className="btn btn-secondary"
                onClick={resetForm}
                disabled={loading}
              >
                <RotateCcw size={20} />
                Reset
              </button>
            </div>
          </form>

          {error && (
            <div className="error">
              {error}
            </div>
          )}
        </div>

        {recipe && (
          <div className="card">
            <div id="recipe-display" className="recipe">
              <div className="recipe-header">
                <h2 className="recipe-title">{recipe.title}</h2>
                <p className="recipe-description">{recipe.description}</p>
              </div>

              <div className="recipe-meta">
                <div className="meta-item">
                  <div className="meta-label">Servings</div>
                  <div className="meta-value">{recipe.servings}</div>
                </div>
                <div className="meta-item">
                  <div className="meta-label">Prep Time</div>
                  <div className="meta-value">{recipe.prepTime}</div>
                </div>
                <div className="meta-item">
                  <div className="meta-label">Cook Time</div>
                  <div className="meta-value">{recipe.cookTime}</div>
                </div>
              </div>

              <div className="recipe-section">
                <h3 className="section-title">
                  <ChefHat size={24} style={{ display: 'inline', marginRight: '8px' }} />
                  Ingredients
                </h3>
                <ul className="ingredients-list">
                  {recipe.ingredients.map((ingredient, index) => (
                    <li key={index} className="ingredient-item">
                      {ingredient}
                    </li>
                  ))}
                </ul>
              </div>

              <div className="recipe-section">
                <h3 className="section-title">Instructions</h3>
                <ol className="instructions-list">
                  {recipe.instructions.map((instruction, index) => (
                    <li key={index} className="instruction-item">
                      <span className="instruction-number">{index + 1}</span>
                      <span className="instruction-text">{instruction}</span>
                    </li>
                  ))}
                </ol>
              </div>

              {recipe.tips && (
                <div className="recipe-section">
                  <div className="tips-section">
                    <h3 style={{ marginBottom: '1rem', color: '#2d3748' }}>Chef's Tips 👨‍🍳</h3>
                    <p className="tips-text">{recipe.tips}</p>
                  </div>
                </div>
              )}

              <div className="recipe-actions">
                <button
                  className="btn btn-secondary"
                  onClick={exportToPDF}
                >
                  <Download size={20} />
                  Export to PDF
                </button>
                <button
                  className="btn btn-primary"
                  onClick={() => setRecipe(null)}
                >
                  <Sparkles size={20} />
                  Generate Another
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default App; 